@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;600;800&display=swap);
html {
  font-family: 'Montserrat',
  sans-serif;
  background-color: #eaeded;
  min-height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.bg{
    background-image: url(/mauna-eCommerceReact/static/media/Bg.389922d5.png);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    height: 400px;
    -webkit-mask-image: linear-gradient(to bottom, #000000, #00000000);
            mask-image: linear-gradient(to bottom, #000000, #00000000);
    position: relative;
    z-index: -1;
}
